body{
    min-height: 100vh;
}
body, p {
    font-weight: 400;
    letter-spacing: .8px;
    line-height: 25px;
    font-family: 'Montserrat', sans-serif;
}
.wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-direction: column;
    height: 100vh;
    background-color: rgba(39, 46, 46, 0.664);

    &__media {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        // background: #ccc url(..//img/bg.jpg) no-repeat center center;
        box-sizing: cover;
    }

    &__media::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 5;
        background-color: rgba(0, 0, 0, 0.2);
    }

    &__media__video {
        position: absolute;
        top: 50%;
        left: 50%;
        min-width: 100%;
        min-height: 100%;
        object-fit: cover;
        transform: translate(-50%, -50%);
    }

    &__content {
        z-index: 10;
        color: #fff;
        text-align: center;
    }
}
